<template>
  <div class="toDoList">
    <div class="describe">{{list.message}}</div>
    <div class="ptime">预计耗费时间：{{time}}</div>
    <div class="ctime">创建时间：{{formatDate}}</div>
    <button @click="toDoList" class="todo">to do</button>
    <button @click="deleteList" class="delete">删除</button>
  </div>
</template>

<script>
export default {
  name: "toDoList",
  props: {
    list: Object
  },
  computed: {
    time() {
      switch(this.list.standard){
        case 'seconds':
          return this.list.time + 's';
        case 'minutes':
          return this.list.time + 'min';
        case 'hours':
          return this.list.time + 'h'
      }
    },
    formatDate() {
      Date.prototype.format = function(fmt) { 
        var o = { 
            "M+" : this.getMonth()+1,                 //月份 
            "d+" : this.getDate(),                    //日 
            "h+" : this.getHours(),                   //小时 
            "m+" : this.getMinutes(),                 //分 
            "s+" : this.getSeconds(),                 //秒 
            "q+" : Math.floor((this.getMonth()+3)/3), //季度 
            "S"  : this.getMilliseconds()             //毫秒 
        }; 
        if(/(y+)/.test(fmt)) {
                fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length)); 
        }
        for(var k in o) {
            if(new RegExp("("+ k +")").test(fmt)){
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
            }
        }
        return fmt; 
      }  
      return this.list.date.format("yyyy-MM-dd hh:mm:ss")
    }
  },
  methods: {
    deleteList() {
      this.$emit('deleteList' , this.list);
    },
    toDoList() {
      if(this.$store.state.isDoing == true){
        alert("当前有任务正在执行，请不要一心二用！");
      }else{
        this.$store.commit('changeState');
        this.$emit("toDoList", this.list);
      }
    }
  }
};
</script>

<style>
.toDoList {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

button.delete{
  background: #f00;
  color: #fff;
}

button.todo{
  background: #99e;
}

.toDoList .todo,.toDoList .delete{
  margin: 0 10px 0 10px;
  flex: 1;
}

.toDoList .ctime, .toDoList .describe,.toDoList .ptime{
  flex: 4;
}

</style>