import Vue from 'vue'
import VueRouter from 'vue-router'
import toDoList from '../views/ToDoLists.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/toDoList'
  },
  {
    path: '/toDoList',
    name: 'toDoList',
    component: toDoList
  },
  {
    path: '/doingList',
    name: 'doingList',
    component: () => import('../views/DoingLists.vue')
  },
  {
    path: '/doneList',
    name: 'doneList',
    component: () => import('@/views/DoneLists.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router