import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    doneLists: [],
    isDoing: false
  },
  mutations: {
    addDoneList(state,list) {
      state.doneLists.push(list)
    },
    changeState(state){
      state.isDoing = !state.isDoing;
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {

  }
})
