<template>
  <div>
    <div class="newTodo">
      <h2>新创建一个TODO</h2>
      <!-- <form @submit="createList" action="##"> -->
      <label>
        做点啥：
        <input
          type="text"
          placeholder="输入待完成的事项"
          name="message"
          id="message"
          @keydown="inputMessage"
        />
      </label>
      <div class="afterMessage">
        <img src="@/assets/img/gantanhao.svg" alt="" /> 请填写此字段。
      </div>
      <label>
        预计耗费时间：
        <input
          type="number"
          name="time"
          id="time"
          min="1"
          placeholder="输入预计耗时"
          @focus="inputTime"
        />
      </label>
      <div class="afterTime">
        <img src="@/assets/img/gantanhao.svg" alt="" /> 请填写此字段。
      </div>
      <select name="standard" id="standard">
        <option value="seconds">秒</option>
        <option value="minutes" selected>分</option>
        <option value="hours">小时</option>
      </select>
      <button @click="createList" class="create">创建</button>
      <!-- <input type="submit" value="创建">
      </form> -->
    </div>
    <h3 v-if="toDoLists.length == 0">无事一身轻</h3>
    <div v-else>
      <h2>待完成</h2>
      <div class="toDoLists">
        <toDoList
          v-for="list in toDoLists"
          :list="list"
          :key="list.message"
          @deleteList="deleteList"
          @toDoList="toDoList"
        ></toDoList>
      </div>
    </div>
  </div>
</template>

<script>
import toDoList from "../components/ToDoList";

export default {
  name: "toDoLists",
  components: {
    toDoList,
  },
  data() {
    return {
      toDoLists: [],
    };
  },
  methods: {
    createList() {
      let messageN = document.getElementById("message"),
        timeN = document.getElementById("time"),
        standardN = document.getElementById("standard"),
        message = messageN.value,
        time = timeN.value,
        standard = standardN.value,
        date = new Date(),
        flag = true;
      if (message == "") {
        document.getElementsByClassName("afterMessage")[0].style.visibility =
          "visible";
        return;
      }
      if (time == "") {
        document.getElementsByClassName("afterTime")[0].style.visibility =
          "visible";
        return;
      }
      for (let list of this.toDoLists) {
        if (list.message == message) {
          alert("请勿重复创建");
          flag = false;
          break;
        }
      }
      if (flag) this.toDoLists.push({ message, time, standard, date });
      messageN.value = "";
      timeN.value = "";
      standardN.children[1].selected = "selected";
    },
    deleteList(list) {
      let lists = this.toDoLists;
      for (let i = 0, len = lists.length; i < len; i++) {
        if (lists[i].message == list.message) {
          this.toDoLists.splice(i, 1);
          break;
        }
      }
    },
    toDoList(list) {
      let lists = this.toDoLists;
      for (let i = 0, len = lists.length; i < len; i++) {
        if (lists[i].message == list.message) {
          this.toDoLists.splice(i, 1);
          break;
        }
      }
      this.$router.push({
        path: "/doingList",
        query: {
          message: list.message,
          time: list.time,
          standard: list.standard,
          date: JSON.stringify(list.date),
        },
      });
    },
    inputMessage() {
      document.getElementsByClassName("afterMessage")[0].style.visibility =
        "hidden";
    },
    inputTime() {
      document.getElementsByClassName("afterTime")[0].style.visibility =
        "hidden";
    },
  },
};
</script>

<style>
* {
  outline: none;
  padding: 0;
  margin: 0;
}

nav, h2, h3, select{
  margin: 10px;
}

nav{
  font-size: 20px;
}

nav a{
  margin-left: 10px;
  margin-right: 10px;
}

input{
  padding: 5px 5px 5px 10px;
  border: none;
  border-bottom: 1px solid #000;
  font-size: 15px;
}

input#message{
  width: 250px;
}

input#time{
  width: 110px;
}

label{
  margin: 0 10px 0 10px;
}

button, select{
  cursor: pointer;
  background: #fff;
  width: 70px;
  height: 25px;
  border: 1px solid #000;
  border-radius: 4px;
  box-shadow: 0 0 1px #333;
}

.newTodo {
  position: relative;
  border-bottom: 1px solid black;
  padding-bottom: 30px;
}

.afterMessage {
  box-shadow: 0 0 10px gray;
  position: absolute;
  top: calc(100% - 20px);
  left: 70px;
  padding: 7px;
  padding-left: 10px;
  font-size: 16px;
  display: inline-block;
  background-color: #fff;
  border-radius: 5px;
  visibility: hidden;
}

.afterMessage img {
  width: 20px;
  height: 20px;
  vertical-align: bottom;
}

.afterMessage::before {
  content: "";
  position: absolute;
  bottom: 100%;
  /* display: block;
  width: 10px;
  height: 10px; */
  border-width: 10px;
  border-top-width: 0px;
  border-color: #fff transparent #fff transparent;
  border-style: dashed dashed solid dashed;
}

.afterTime {
  box-shadow: 0 0 10px gray;
  position: absolute;
  top: calc(100% - 20px);
  left: 490px;
  padding: 7px;
  padding-left: 10px;
  font-size: 14px;
  display: inline-block;
  background-color: #fff;
  border-radius: 5px;
  visibility: hidden;
}

.afterTime img {
  width: 20px;
  height: 20px;
  vertical-align: bottom;
}

.afterTime::before {
  content: "";
  position: absolute;
  bottom: 100%;
  /* display: block;
  width: 10px;
  height: 10px; */
  border-width: 10px;
  border-top-width: 0px;
  border-color: #fff transparent #fff transparent;
  border-style: dashed dashed solid dashed;
}

.toDoList:nth-child(2n+1){
  background: #aaa;
}
</style>